import { faEnvelope } from '@fortawesome/free-regular-svg-icons/faEnvelope';
import { faCalendarAlt } from '@fortawesome/free-regular-svg-icons/faCalendarAlt';

library.add(faAngleDoubleDown,
  faAngleDoubleUp,
  faLightbulb,
  faHeart,
  faRoad,
  faEye,
  faMapSigns,
  faMapMarkerAlt,
  faMapMarker,
  faQuestionCircle,
  faArrowCircleRight,
  faSync,
  faArrowCircleDown,
  faHandHoldingHeart,
  faMicroscope,
  faBrain,
  faMugHot,
  faExclamation,
  faDumbbell,
  faChess,
  faPoo,
  faHome,
  faTrophy,
  faCoffee,
  faGlobe,
  faBook,
  faHandshake,
  faWrench,
  faTwitter,
  faLinkedinIn,
  faLinkedin,
  faFacebookF,
  faInstagram,
  faGithub,
  faPiedPiperAlt,
  faFly,
  faCalendarAlt,
  faEnvelope);

// Watch for DOM changes and replace <i> tags with SVGs
dom.watch();

// Also process any existing icons on page load
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => {
    dom.i2svg();
  });
} else {
  // DOM is already loaded
  dom.i2svg();
}
